<template>
    <div class="ToconfigureHubei students">
        <div class="ToconfigureHubei-dv">
            <span class="spano">试卷配置</span> > <span class="spant">配置考查内容与课程目标对应关系</span>
        </div>
        <div class="ToconfigureHubei-dv1">
            <h2>《{{coursename}}》课程试卷配置</h2>
            <div class="ToconfigureHubei_dv">
                <ul>
                    <li>
                        <span class="ToconfigureHubei_span3">1</span>
                        <span class="ToconfigureHubei_span2">课程目标评价依据</span>
                    </li>
                    <li style="color: #fff;">••••＞</li>
                    <li>
                        <span class="ToconfigureHubei_span1">2</span>
                        <span class="ToconfigureHubei_span2">考查内容与课程目标对应关系</span>
                    </li>
                </ul>
            </div>
        </div>
        <el-form :model="ruleForm2" status-icon :rules="rules2" ref="ruleForm2"  class="demo-ruleForm">
            <el-form-item  prop="ratio">
                <div style="padding-top: 30px;background-color: #fff;">
                    <div class="ToconfigureHubei-dv2" v-for="(item, indexq) in AllList" :key="indexq"> 
                        <h2 class="ToconfigureHubei-dv2h2">评价依据{{indexq+1}}{{item.examine_name}}考试：考查内容与课程目标对应关系及权重</h2>
                        <table v-if="isLoading" cellspacing="0"  cellpadding="0"  border="0" class="el-table el-table--border cc-table">
                            <tr>
                                <th style=" width:90px; text-align: center;">编号</th>
                                <th style=" width:500px; text-align: center;">考查内容（可以是试卷题号或考查知识点）</th>
                                <th style="padding-left: 20px;text-align: center;" v-for="(items, index) in item.titles" :key="index">{{items.couseGoalName}}</th>
                            </tr>
                            <tr v-for="(iteme, indexs) in item.contentList" :key="indexs">
                                <td style="padding-left: 30px;text-align: inherit;" @mousemove="addad" @mouseleave="assaf">{{indexs +1 }}
                                    <span class="table-sop" v-show="falge3"><i @click="delratio(iteme, indexs,indexq)" v-show="falge3" class="el-icon-circle-close "></i></span>
                                </td>
                                <td style="width: 189px;" >
                                    <input type="text" v-model="iteme.name" autofocus="autofocus" ref='id' style="height: 30px;margin-left: 8px; border: 0;">
                                </td>
                                <td v-for="(vo,index) in item.titles" :key="index" >
                                    <el-input-number v-model="iteme[`ratio_${index}`]" controls-position="right" :min="0" :max="1" placeholder="权重"></el-input-number>
                                </td>
                            </tr>
                        </table>
                        <div @click="addExamine(indexq)" class="setCourseTitle add-examine-button">
                            <SvgIcon icon-class="icontianjia" size="20px" class="setCourseTitle_svg"></SvgIcon>
                            <span class="setCourseTitle_span">添加考查题目</span>
                        </div>
                        <div class="setCourseTitle tishi">* 每道题分目标权重总和等于1</div>
                    </div>
                </div>
            </el-form-item>
        </el-form>
        <div class="ToconfigureHubei-dv3">
            <template>
                 <div class="search--btn"  @click="submit(next = 0,'ruleForm2')" style="margin: 0 0 0 20px">保 存</div>
                <el-button type="primary" @click="submit(next = 1,'ruleForm2')">提交</el-button>
            </template>
        </div>
    </div>
</template>
<script>
var floatObj = function() {
    function isInteger(obj) {
        return Math.floor(obj) === obj
    }
    function toInteger(floatNum) {
        var ret = {times: 1, num: 0}
        if (isInteger(floatNum)) {
            ret.num = floatNum
            return ret
        }
        var strfi  = floatNum + ''
        var dotPos = strfi.indexOf('.')
        var len    = strfi.substr(dotPos+1).length
        var times  = Math.pow(10, len)
        var intNum = parseInt(floatNum * times + 0.5, 10)
        ret.times  = times
        ret.num    = intNum
        return ret
    }
    function operation(a, b, op) {
        var o1 = toInteger(a)
        var o2 = toInteger(b)
        var n1 = o1.num
        var n2 = o2.num
        var t1 = o1.times
        var t2 = o2.times
        var max = t1 > t2 ? t1 : t2
        var result = null
        switch (op) {
            case 'add':
                if (t1 === t2) { // 两个小数位数相同
                    result = n1 + n2
                } else if (t1 > t2) { // o1 小数位 大于 o2
                    result = n1 + n2 * (t1 / t2)
                } else { // o1 小数位 小于 o2
                    result = n1 * (t2 / t1) + n2
                }
                return result / max
            case 'subtract':
                if (t1 === t2) {
                    result = n1 - n2
                } else if (t1 > t2) {
                    result = n1 - n2 * (t1 / t2)
                } else {
                    result = n1 * (t2 / t1) - n2
                }
                return result / max
            case 'multiply':
                result = (n1 * n2) / (t1 * t2)
                return result
            case 'divide':
                result = (n1 / n2) * (t2 / t1)
                return result
        }

    }
    // 加减乘除的四个接口
    function add(a, b) {
        return operation(a, b, 'add')
    }
    function subtract(a, b) {
        return operation(a, b, 'subtract')
    }
    function multiply(a, b) {
        return operation(a, b, 'multiply')
    }
    function divide(a, b) {
        return operation(a, b, 'divide')
    }
    return {
        add: add,
        subtract: subtract,
        multiply: multiply,
        divide: divide
    }
}();
export default {
    name: "TestWorld",
    data(){
        var validatePass = (rule, value, callback) => {
            let self = this
            const newArr=[]
            self.AllList.forEach((item,index) => {
                item.contentList.forEach((vo,num)=>{   
                    const obj={
                        indexVal:`${index}_${num}`,
                        contentList:[]
                    }
                    vo.newgoalratio.forEach((v,n )=>{        
                        obj.contentList.push({
                            ratio:vo[`ratio_${n}`] || 0
                        })          
                    })
                    newArr.push(obj)
                })
            });
            function recursiveCheck(arr) {
                let sum = 0;
                for (let vo of arr) {
                    const ratio= vo.ratio || 0
                    sum=floatObj.add(sum, ratio)
                }
                if (sum === 1 ) {
                    return "next";
                } else {
                    return callback(new Error("每道题分目标权重总和等于1"));
                }
            }
            for (let i = 0, length = newArr.length; i < length; i++) {
                if (recursiveCheck(newArr[i].contentList) === "next") {
                    if (newArr[i + 1]) {
                        recursiveCheck(newArr[i + 1].contentList);
                    } else {
                        callback();
                        break;
                    }
                }
            }
        };
        return{
            content:'',
            falge3:false,
            isLoading:false,
            AllList:[],
            videolist:[],
            course_id:this.$route.query.id,
            coursename:this.$route.query.coursename,
            grade:this.$route.query.grade,
            arrid:'',
            ruleForm2: {
                pass: '',
                checkPass: '',
                age: ''
            },
            rules2: {
                ratio: [
                    { validator: validatePass, trigger: 'blur' }
                ],
            }
        }
    },
    methods:{
        //  数据回显
        getexamgoal(){
            this.$get(`/basic/traExamContent/contentBindGoalView/${this.course_id}/${this.$route.query.scheduId}`).then(res=>{
                if(res.status == 1){    
                    let arr = ''   
                    let newarr = []
                    res.data.forEach(element => {
                        element.contentList.forEach((vo,io) => { 
                            arr = element.titles.length
                            if(element.titles && element.titles.length){
                                vo.arr=vo.ratio
                                vo.newgoalratio = vo.goalratio.reverse()
                                element.titles.forEach((co,inv) => {
                                    vo.newgoalratio.forEach((doc,i) => {
                                        if(doc.goalId === co.couseGoalId){
                                            vo[`ratio_${inv}`]= doc.ratio || 0

                                        }else {
                                            // vo[`ratio_${inv}`]='0'
                                        }
                                        // doc.examGoalId =  element.titles[io].couseGoalId
                                    });
                                });
                            }   
                        });
                    });
                    this.arrid = arr
                    this.AllList = res.data
                    this.isLoading=true
                }
            })
        },
        addExamine(indexq) {  //添加评价依据
            let topRelations = JSON.parse(JSON.stringify( this.AllList[indexq].titles))
            this.$get('/basic/courseGoal/getId').then(res=>{
                var newValue = {
                        name:"",
                        examId: "",
                        newgoalratio:topRelations || []
                    };
                newValue.newgoalratio.forEach((arr,index)=>{
                    arr.goalId = arr.couseGoalId
                    arr.contentId = res
                })
                this.AllList[indexq].contentList.push(newValue);
            })
        },
        addad(){ //鼠标移入移出
            this.falge3 = true
        },
        assaf(){ //鼠标移入移出
            this.falge3 = false
        },
        setCourseDel(id, index) {
            this.$deletes(`/basic/traExamContent/del/${id}`).then(res => {
                if (res.status === 1) {
                    this.$notify.success({
                        title: "success",
                        duration: 1500,
                        message: "删除成功"
                    });
                    this.getexamgoal()
                    //  this.examineArr.splice(index, 1)
                } else {
                    this.$notify.error({
                        title: "error",
                        duration: 1500,
                        message: res.message
                    })
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        delratio(vo, index,indexq){ // 删除一行评价依据
            if (vo.id) {
                this.setCourseDel(vo.id, index)
            } else {
                this.$notify.success({
                    title: "success",
                    duration: 1500,
                    message: "删除成功"
                });
                this.AllList[indexq].contentList.splice(index, 1)
            }
        },
        submit(next,formName){ // 保存提交
        this.$refs[formName].validate(valid => {
            if (valid) {
                let list= [];
                let num = 0
                this.AllList.forEach((item,index) => {
                    item.contentList.forEach((items,i) => {
                        items.newgoalratio.forEach((doc,inv) => {
                            list.push({
                                name:items.name ,
                                courseId:this.course_id,
                                examineId:item.examine_id,
                                //examGoalId:doc.goalId,
                                 examGoalId:item.titles[inv].couseGoalId,
                                ratio:items[`ratio_${inv}`] || 0,
                                id:items.id || '',
                                contentId:doc.contentId
                            })
                            
                        });
                    })
                });
                this.$post('/basic/traExamContent/contentBindGoal',list).then(res=>{
                    if(res.status === 1){
                        if(next === 0){ // 保存
                            this.getexamgoal()
                            this.$notify.success({
                                title: 'success',
                                duration:1500,
                                message: '保存成功'
                            })
                        }else { // 提交
                            //this.$router.push({path: '/Construction/TestPaperConfiguration'}); 
                            this.$router.push({path: '/Construction/TestPaperConfigurationHb'}); 
                        }
                    }else {
                        this.$notify.error({
                            title: 'error',
                            duration:1500,
                            message: res.message
                        })
                    }
                })
            } else {  
                    this.$notify.warning({
                        title: 'warning',
                        duration:1500,
                        message: '每道题分目标权重总和等于1'
                    })
                }
            });
        },
    },
    mounted(){
        this.getexamgoal()
    }
}
</script>
